import styled, { keyframes } from "styled-components"

const threedotsvertical = keyframes`
    0% {
      box-shadow: 0 12px 0 -12px #7286a7; 
    }
    100% {
      box-shadow: 0 12px 0 #7286a7; 
    }
`

const StyledWrap = styled.div`
  width: ${props => props.size}px;
  height: ${props => props.size}px;
  border-radius: 50%;
  position: relative;
  animation: ${props => threedotsvertical} 0.9s ease alternate infinite;
  animation-delay: 0.36s;
  margin: -${props => props.size * 2}px auto 0;
  &::after {
    content: "";
    position: absolute;
    width: ${props => props.size}px;
    height: ${props => props.size}px;
    border-radius: 50%;
    animation: ${props => threedotsvertical} 0.9s ease alternate infinite;
    right: -${props => props.size + 2}px;
    animation-delay: 0.54s;
  }
  &::before {
    content: "";
    position: absolute;
    width: ${props => props.size}px;
    height: ${props => props.size}px;
    border-radius: 50%;
    animation: ${props => threedotsvertical} 0.9s ease alternate infinite;
    left: -${props => props.size + 2}px;
    animation-delay: 0.18s;
  }
`
export default function UILoading(props) {
  return <StyledWrap size={props.size || 28}></StyledWrap>
}
